___CSS_LOADER_EXPORT___.push([module.id, `.videoCard {
  width: 580px;
}
@media (max-width: 1024px) {
  .videoCard {
    width: 100%;
  }
}
.videoCard__title {
  margin-top: 16px;
  color: #41456b;
  color: var(--primary---content, #41456b);
  font-size: 25px;
  font-weight: 700;
  line-height: 140%; /* 35px */
  letter-spacing: 0.5px;
}
@media (max-width: 1024px) {
  .videoCard__title {
    margin-top: 12px;
  }
}
@media (max-width: 640px) {
  .videoCard__title {
    margin-top: 4px;
    font-size: 20px;
  }
}
.videoCard__play {
  width: 53px;
  height: 53px;
  position: absolute;
  top: 49%;
  left: 51%;
  transform: translate(-53%, -47%);
  cursor: pointer;
}
@media (max-width: 640px) {
  .videoCard__play {
    width: 35px;
    height: 35px;
  }
}`, "",{"version":3,"sources":["webpack://./src/components/videoCard/index.scss"],"names":[],"mappings":"AAAA;EACE,YAAA;AACF;AACE;EAHF;IAII,WAAA;EAEF;AACF;AAGE;EACE,gBAAA;EAEA,cAAA;EAAA,wCAAA;EAEA,eAAA;EACA,gBAAA;EACA,iBAAA,EAAA,SAAA;EACA,qBAAA;AAHJ;AAKI;EAVF;IAWI,gBAAA;EAFJ;AACF;AAII;EAdF;IAeI,eAAA;IACA,eAAA;EADJ;AACF;AAIE;EACE,WAAA;EACA,YAAA;EAEA,kBAAA;EACA,QAAA;EACA,SAAA;EACA,gCAAA;EAEA,eAAA;AAJJ;AAMI;EAXF;IAYI,WAAA;IACA,YAAA;EAHJ;AACF","sourcesContent":[".videoCard {\n  width: 580px;\n\n  @media (max-width: 1024px) {\n    width: 100%;\n  }\n\n  &__img {\n  }\n\n  &__title {\n    margin-top: 16px;\n\n    color: var(--primary---content, #41456b);\n\n    font-size: 25px;\n    font-weight: 700;\n    line-height: 140%; /* 35px */\n    letter-spacing: 0.5px;\n\n    @media (max-width: 1024px) {\n      margin-top: 12px;\n    }\n\n    @media (max-width: 640px) {\n      margin-top: 4px;\n      font-size: 20px;\n    }\n  }\n\n  &__play {\n    width: 53px;\n    height: 53px;\n\n    position: absolute;\n    top: 49%;\n    left: 51%;\n    transform: translate(-53%, -47%);\n\n    cursor: pointer;\n\n    @media (max-width: 640px) {\n      width: 35px;\n      height: 35px;\n    }\n  }\n}\n"],"sourceRoot":""}]);
